<template>
  <div>
    <v-btn @click="toggleWhatApp" outlined>
      {{ showWhatApp ? 'Quit' : 'Load' }} 
    </v-btn>
    <ContactList :projectKey="selectedProject.projectKey" :enDate="formattedDate" v-if="showWhatApp" />
    
    <v-container>
      <v-row>
        <v-col>
          <p><strong>Project Name:</strong> {{ projectName }}</p>
        </v-col>
      </v-row>
      
      <v-row>
        <v-col>
       
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <p>Formatted Date: https://alvintkb.github.io/?p=gyp{{ formattedDate }}{{ selectedProject.projectKey }}</p>
          <p>Duration {{ adDuration() }}</p>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-btn @click="toggleUpload" outlined>
            {{ showUpload ? 'Hide' : 'Show' }} Upload
          </v-btn>
          <uploadStrapi2 v-if="showUpload" :projectKey="projectKey" />
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import uploadStrapi2 from './uploadStrapi.vue';
import ContactList from './ContactList.vue';
import DataSpringVale from './DataSpringVale.json';
import DataNexus from './DataNexus.json';
import DataAcadia from './DataAcadia.json';
import DataTm from './DataTm.json';


export default {
  components: {
    uploadStrapi2,
    ContactList,

  },
  mounted() {
    this.formatCurrentDate();
  },
  methods: {
    toggleWhatApp() {
      this.showWhatApp = !this.showWhatApp;
    },
    toggleUpload() {
      this.showUpload = !this.showUpload;
    },
    isExpired() {
      return this.adDuration() > 48;
    },
    adDuration() {
      let currenthour = this.getCurrentTotalHours();
      let oldhourstr = this.convertLetterStringToNumbers(this.urlParam.slice(3, 9));
      let oldhour = this.convertToHour(oldhourstr);
      let hour = currenthour - oldhour;
      if (this.urlParam.slice(0, 3) === "prj") {
        return 0;
      }
      if (this.urlParam.slice(0, 3) === "gyp") {
        return currenthour - oldhour;
      }
      return hour;
    },
    getCurrentTotalHours() {
      const now = new Date();
      const currentMonth = now.getMonth() + 1;
      const currentDay = now.getDate();
      const currentHour = now.getHours();
      const hoursFromMonths = (currentMonth - 1) * 30 * 24;
      const hoursFromDays = (currentDay - 1) * 24;
      const totalHours = hoursFromMonths + hoursFromDays + currentHour;
      return totalHours;
    },
    convertToHour(str) {
      const month = parseInt(str.substr(0, 2));
      const day = parseInt(str.substr(2, 2));
      const hour = parseInt(str.substr(4, 2));
      const totalHour = (month - 1) * 30 * 24 + (day - 1) * 24 + hour;
      return totalHour;
    },
    formatCurrentDate() {
      const currentDate = new Date();
      this.formattedDate = this.formatDateToString(currentDate);
    },
    numberToLetter(num) {
      return String.fromCharCode(97 + parseInt(num));
    },
    letterToNumber(letter) {
      return letter.charCodeAt(0) - 97;
    },
    convertNumberStringToLetters(numberStringInput) {
      if (/^\d+$/.test(numberStringInput)) {
        return `${[...numberStringInput].map(this.numberToLetter).join('')}`;
      } else {
        return 'Please enter a valid string of digits.';
      }
    },
    convertLetterStringToNumbers(letterStringInput) {
      if (/^[a-z]+$/.test(letterStringInput)) {
        return `${[...letterStringInput].map(this.letterToNumber).join('')}`;
      } else {
        return 'Please enter a valid string of lowercase letters.';
      }
    },
    formatDateToString(date) {
      const month = date.getMonth() + 1;
      const day = date.getDate();
      const hr = date.getHours();
      const monthStr = month < 10 ? '0' + month : month.toString();
      const dayStr = day < 10 ? '0' + day : day.toString();
      const hrStr = hr < 10 ? '0' + hr : hr.toString();
      let formattedDate = monthStr + dayStr + hrStr;
      let ss = this.convertNumberStringToLetters(formattedDate);
      return ss;
    },
    checkString(searchString, substrings) {
      return substrings.some(substring => searchString.includes(substring));
    }
  },
  computed: {
    projectName() {
      if (this.checkString(this.urlParam, ["prj", "wap"])) {
        return this.urlParam.slice(3);
      } else {
        return this.urlParam.slice(9);
      }
    },
    projectKey() {
      if (this.checkString(this.urlParam, ["prj", "wap"])) {
        return this.urlParam.slice(3);
      } else {
        return this.urlParam.slice(21);
      }
    },
    phoneNumber() {
      if (this.urlParam.slice(0, 3) === "gyp") {
        return this.urlParam.slice(9, 21);
      } else {
        return "123456789012";
      }
    },
    selectedProject() {
      return this.imageData.find(item => item.projectKey.toLowerCase() === this.projectKey.toLowerCase()) || { urls: [] };
    }
  },
  props: {
    urlParam: {
      type: String,
      required: true,
    }
  },
  data() {
    return {
      showWhatApp: false,
      showUpload: false,
      formattedDate: '',
      isProduction: process.env.VUE_APP_ENV === 'production',
      blog: {
        title: "Maximizing Rental Yield: Navigating the High-Priced Property Market",
        content: "Given the current scenario of soaring property prices, it's crucial to consider investment options that promise substantial rental yields. While the market may seem daunting with properties priced at around 700k, prioritizing investments with a return yield of over 10% can be a game-changer. Instead of succumbing to FOMO, focus on properties that offer good rental yield potential, even if they are not in the early phases of development. This strategic approach can help you make a prudent decision amidst the frenzy of the property market."
      },
      imageData: [...DataSpringVale, ...DataNexus, ...DataTm, ...DataAcadia],
      showLaunchButton: false,
    };
  },
};
</script>

