<template>
  <div id="app">
   
   
    <einvoiceFront/>
    <!-- <apiImage/> -->
    <!-- Debugging info -->
    <!-- <p>URL Param: {{ urlParam }}</p> -->
    
    <!-- Render HelloWorld component if urlParam is 'tm' -->
    <div v-if="normalizedUrlParam === 'tm'">
      <HelloWorld msg="Welcome to Your Vue.js App"/>
    </div>

    <div v-else-if="normalizedUrlParam === 'ta'">
      <mainMenu msg="Welcome to Your Vue.js App"/>
    </div>
        
    <div v-else-if="first3Char === 'jsn'">
      <g_json :urlParam="urlParam"/>
    </div>

    <!-- Render HelloWorld2 component if urlParam is 'eum' -->
    <div v-else-if="first3Char === 'gyp' || first3Char === 'prj'">
      <APage :urlParam="urlParam"/> 
    </div>
 
    <div v-else-if="first3Char === 'wap'">
      <WhatApp :urlParam="urlParam"/> 
    </div>


    
    <!-- Display a message if the urlParam is neither 'tm' nor 'eum' -->
    <div v-else>
      <h1>nothing here 2.0</h1>
    </div>
  </div>
</template>

<script>
import mainMenu from './components/mainMenu.vue';
import WhatApp from './components/WhatApp.vue';
import HelloWorld from './components/HelloWorld.vue';
import APage from './components/APage.vue';
import g_json from './components/g_json.vue';

// import einvoiceFront from './components/einvoiceFront.vue';
// import apiImage from './components/apiImage.vue';


export default {
  name: 'App',
  components: {
mainMenu,
    // einvoiceFront,
    // apiImage,
    WhatApp,
    HelloWorld,
    APage,
    g_json
  },

  data() {
    return {
      urlParam: ''
    }
  },
  computed: {
    normalizedUrlParam() {
      return this.urlParam.toLowerCase();
    },
    first3Char() {
      return this.normalizedUrlParam.slice(0, 3);
    }
  },
  created() {
    // Fetch URL parameter
    const urlParams = new URLSearchParams(window.location.search);
    this.urlParam = urlParams.get('p') || '';
    
    console.log('URL Param:', this.urlParam);  // Debugging log
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
