      //  notionVideoLink:   'https://file.notion.so/f/f/168abcb4-3782-43cf-966c-1436aa07fb5e/83fa0d72-dc26-4a68-8ad0-e70b06672b1d/showroom.mp4?id=e3d45a6a-5dc0-411e-b3bc-1fb56476d8d3&table=block&spaceId=168abcb4-3782-43cf-966c-1436aa07fb5e&expirationTimestamp=1717473600000&signature=p16wToiTMHLKTuova-pnXLEO2G3G91s9l6ZgAtRHA8s&downloadName=showroom.mp4'
      //  notionVideoLink: 'https://third-addition-a89.notion.site/Victova-b419ec46b07a49f7aabfe010ff62e4d6'
      //      notionVideoLink:'https://file.notion.so/f/f/168abcb4-3782-43cf-966c-1436aa07fb5e/08f0c4bc-ec08-4d1a-aebe-ffe461b1a6ea/VID_20240115_204751.mp4?id=f94ab74d-f708-44e5-8577-8e5632c23462&table=block&spaceId=168abcb4-3782-43cf-966c-1436aa07fb5e&expirationTimestamp=1717459200000&signature=V9h2yNe1p0JXUW0sQ_0suHzIOLTME3eTxoTO3aoLiRo&downloadName=VID_20240115_204751.mp4'
      // videoUrl: 'https://file.notion.so/f/f/168abcb4-3782-43cf-966c-1436aa07fb5e/5fc7fdeb-c64b-46e0-92d6-1378fa2a48cd/NEXUS_Taman_Pertama.mov?id=c6678f2d-e9ef-4601-b4b2-fca29279e55d&table=block&spaceId=168abcb4-3782-43cf-966c-1436aa07fb5e&expirationTimestamp=1717480800000&signature=I81h8PHOUIgQ0gWS3QixTA-_VBWk1SpwunkHsNYHgTI'
      //  videoUrlionVideoLink:'https://file.notion.so/f/f/168abcb4-3782-43cf-966c-1436aa07fb5e/08f0c4bc-ec08-4d1a-aebe-ffe461b1a6ea/VID_20240115_204751.mp4?id=f94ab74d-f708-44e5-8577-8e5632c23462&table=block&spaceId=168abcb4-3782-43cf-966c-1436aa07fb5e&expirationTimestamp=1717459200000&signature=V9h2yNe1p0JXUW0sQ_0suHzIOLTME3eTxoTO3aoLiRo&downloadName=VID_20240115_204751.mp4'

    };
  },
  computed: {

    projectName() {
      if (this.urlParam.slice(0, 3) === "prj")
        return this.urlParam.slice(3);
      else
        return this.urlParam.slice(9);
    },
    projectKey() {

      if (this.urlParam.slice(0, 3) === "prj")
        return this.urlParam.slice(3);
      else
        return this.urlParam.slice(21);
    },
    phoneNumber() {
      if (this.urlParam.slice(0, 3) === "gyp")
        return this.urlParam.slice(9, 21);
      else
        return "123456789012";
    },

    selectedProject() {
      return this.imageData.find(item => item.projectKey.toLowerCase() === this.projectKey.toLowerCase()) || { urls: [] };
    }
  }
}
