<template>


  <div>
    <div class="fixed-header">
      <v-container>
        <v-data-table
          :headers="[{ title: 'Msg' }, { title: 'No' }, { title: 'project' }, { title: 'source' }, { title: 'name' }, { title: 'Phone' }, { title: 'Occupation' }, { title: 'Email' }, { title: 'Message' }]"
          :items="[currentContact]" class="elevation-1" hide-default-footer>
          <!-- <template v-slot:item="{ item }"> -->

          <template v-slot:item="{ item }">
            <tr>
              <td width="8%"> <input type="text" id="sendmsg" v-model="sendmsg"></td>
              <td> {{ item.no }}</td>
              <td> {{ item.project }}</td>
              <td> {{ item.source }}</td>
              <td style="font-size: 18px;font-weight: bold;"> {{ item.name }}</td>
              <td> {{ item.phoneNumber }}</td>
              <td style="font-size: 18px;font-weight: bold;">{{ item.occupation }}</td>
              <td> {{ item.email }}</td>

              <td> {{ item.message }}</td>
              <!-- <td style="color: blue;">{{ item.interaction }}</td> -->
            </tr>
          </template>
        </v-data-table>

        <h2 style="color: blue;"> {{ currentContact.interaction }}</h2>


      </v-container>
      <div>

        <!-- <label for="sendmsg">Send Message:</label>
        <input type="text" id="sendmsg" v-model="sendmsg"> -->

        <input class="bordered-input" type="number" id="jumpIndex" v-model="jumpIndex"> <button class="bordered-input"
          @click="jumpToIndex">By Row No.</button> <button class="bordered-input" @click="findNumber">By Phone
          Number</button>
      </div>
    </div>
    <br /><br /><br />
    <br /><br /><br />
    <br /><br /><br />

    <button @click="sendwhatsappmessage">Send via WhatsApp</button>
    <button @click="startProcessing">Start</button>
    <button @click="reset">Reset</button>
    <input type="file" @change="loadCSVFile" accept=".csv">
    <button @click="exportToCSV">Export to CSV</button>


    <p v-if="error" class="error">{{ error }}</p>

    <h2>Contacts List V1.00</h2>


    <!-- <v-data-table
    @click:row="rowClick" :headers="[{ title: 'Msg' }, { title: 'No' }, { title: 'project' }, { title: 'source' }, { title: 'name' }, { title: 'Phone' }, { title: 'Occupation' }, { title: 'Email' }, { title: 'Message' }]"
      :items="contacts" class="elevation-1" hide-default-footer> -->
    <v-data-table :headers="headers" :items="contacts" class="elevation-1"
      hide-default-footer>
     
        <template v-slot:item="{ item, index }">
        <!-- <tr v-for="(contact, index) in contacts" :key="index" :class="{ 'selected': currentIndex === index }"> -->
          <tr :class="{ 'highlight': currentIndex === index }" @click="rowClick(index)">
          <td width="3%">
            <v-btn @click="sendViaWhatsApp(item)" color="primary" small>
              Send
            </v-btn>
          </td>
          <td width="3%">
            <v-btn @click="makeCall(item)" color="primary" small>
              Call
            </v-btn>
          </td>
          <td width="3%">{{ item.no }}</td>

          <!-- Display index number -->
          <!-- <td style="font-size: 8px;">{{ contact.project }}</td>
          <td width="3%" style="font-size: 8px;">{{ contact.source }}</td> -->
          <!-- <td style="font-size: 10px;">{{ contact.name }}</td> -->
          <td width="7%">
            <input type="text" v-model="item.name">
          </td>

          <td width="3%">
            <input type="text" v-model="item.language">
          </td>
          <td width="8%">
            <input type="text" v-model="item.phoneNumber">
          </td>

          <td width="42%">
            <textarea v-model="item.interaction"></textarea>
          </td>

          <td width="3%">
            <input type="text" v-model="item.status">
          </td>

          <td width="4%">
            <input type="text" v-model="item.doc">
          </td>

          <td width="3%">
            <input type="text" v-model="item.called">
          </td>

          <td width="3%">
            <input type="text" v-model="item.contacted">
          </td>

          <td width="3%" style="font-size: 12px;">
            <input type="text" v-model="item.na1">
          </td>
          <td>{{ item.date }}</td>
          <td width="6%">
            <select v-model="item.lastCall">
              <option value="">Select a date</option>
              <option v-for="date in availableDates" :key="date" :value="date">
                {{ date }}
              </option>
            </select>
          </td>
          <td width="6%">
            <select v-model="item.nextCall">
              <option value="">Select a date</option>
              <option v-for="date in availableDates" :key="date" :value="date">
                {{ date }}
              </option>
            </select>
          </td>
          <!-- <td>{{ contact.lastCall }}</td>
          <td>{{ contact.nextCall }}</td> -->
        </tr>
      </template>
    </v-data-table>
  </div>
</template>


<script>
//
import Papa from 'papaparse';
import { ref, watchEffect, watch, onMounted } from 'vue';
import { useMutation } from '@vue/apollo-composable';
import { gql } from '@apollo/client/core';
import cloneDeep from 'lodash/cloneDeep'; // Ensure lodash is properly imported

const UPDATE_CALLME = gql`
  mutation UpdateCallme($data: CallmeInput!) {
    updateCallme(data: $data) {
      data {
        id
        attributes {
          name
          phoneNumber
          status
          interaction
          lastCall
          nextCall
          no
        }
      }
    }
  }
`;

export default {
  name: 'ContactList',
  props: {
    projectKey: {
      type: String,
      required: true
    },
    enDate: {
      type: String,
      required: true
    }
  },

  setup() {
    const contacts = ref([]);
    const currentIndex = ref(0); // Initialize currentIndex
    const dataRestored = ref(false); // Flag to check if data has been restored

    // const convertYMD2DMY = (dateString) => {
    //   let [year, month, day] = dateString.split('-');
    //   month = month.padStart(2, '0');
    //   day = day.padStart(2, '0');
    //   return `${day}-${month}-${year.substring(2)}`;
    // };
    const todayDDMMYYYY = () => {
      const date = new Date();
      const day = String(date.getDate()).padStart(2, '0'); // Add leading zero if needed
      const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are 0-indexed
      const year = date.getFullYear();

      return `${day}/${month}/${year}`;
    };

    const convertDMY2YMD = (dateString) => {
      let [day, month, year] = dateString.split('-');
      month = month.padStart(2, '0');
      day = day.padStart(2, '0');
      return `${year}-${month}-${day}`;
    };

    const { mutate: updateCallme } = useMutation(UPDATE_CALLME);


    const makeCall = async (contact) => {
      try {
        await new Promise(resolve => setTimeout(resolve));
        let { name, phoneNumber, status, interaction, lastCall, nextCall, no } = contact;
        lastCall = convertDMY2YMD(lastCall);
        nextCall = convertDMY2YMD(nextCall);
        const variables = {
          data: { name, phoneNumber, status, interaction, lastCall, nextCall, no }
        };

        const { data } = await updateCallme(variables);

        console.log('Updated callme:', data.updateCallme.data);

        contact.called = (function (calledString) {
          // Attempt to convert the string to a number
          const numberValue = parseInt(calledString, 10);

          // Check if conversion was successful (is a number)
          if (!isNaN(numberValue)) {
            return numberValue + 1; // Increment the number
          } else {
            return 1; // Set to zero if not numeric
          }
        })(contact.called);

        contact.date = todayDDMMYYYY();
        // Perform any additional logic with the updated data

      } catch (error) {
        console.error('Error updating callme:', error);
        // Handle error appropriately
      }
      currentIndex.value = contacts.value.findIndex(c => c.no === contact.no);
    }

    const saveDataToLocalStorage = () => {
      console.log('Attempting to save data to local storage...');
      if (dataRestored.value) {
        try {
          const dataToSave = {
            contacts: contacts.value,
            // Add other properties as needed
          };
          localStorage.setItem('appData', JSON.stringify(dataToSave));
          console.log('Data saved to local storage:', dataToSave);
        } catch (error) {
          console.error('Error saving to local storage:', error);
        }
      } else {
        console.log('Data not saved: dataRestored.value is false');
      }
    };


    const restoreDataFromLocalStorage = () => {
      console.log('Attempting to restore data from local storage...');
      const savedData = localStorage.getItem('appData');
      if (savedData) {
        try {
          console.log('Saved data found in local storage:', savedData);
          const parsedData = JSON.parse(savedData);
          contacts.value = parsedData.contacts || [];
          // Restore other properties as needed
          dataRestored.value = true;
          console.log('Data restored from local storage:', parsedData);
        } catch (error) {
          console.error('Error parsing data from local storage:', error);
        }
      } else {
        console.log('No saved data found in local storage.');
      }
    };

    onMounted(() => {
      restoreDataFromLocalStorage();

      // Additional initialization logic if needed
    });

    // Watch contacts for changes and save to localStorage
    watchEffect(() => {
      console.log('watchEffect triggered, contacts:', contacts.value);
      //  saveDataToLocalStorage();
    });
    let initialContacts = cloneDeep(contacts.value);
    // Watch for changes in contacts
    watch(contacts, (newContacts) => {
      console.log('Contacts changed');

      // Check if there are any changes compared to the initial state
      let isDirty = false;

      // Ensure initialContacts and newContacts are arrays of the same length
      if (newContacts.length !== initialContacts.length) {
        isDirty = true;
      } else {
        for (let i = 0; i < newContacts.length; i++) {
          const newContact = newContacts[i];
          const initialContact = initialContacts[i];

          // Check if newContact or initialContact is undefined
          if (!newContact || !initialContact) {
            isDirty = true;
            break;
          }

          // Compare each field of newContact with initialContact
          for (const key in newContact) {
            if (newContact[key] !== initialContact[key]) {
              console.log(`Field '${key}' in contact ${newContact.id} changed`);
              isDirty = true;
              break; // No need to check further fields if one is different
            }
          }

          if (isDirty) break; // No need to check further contacts if one is different
        }
      }

      // If any data is dirty, save to localStorage and update the initialContacts
      if (isDirty) {
        dataRestored.value = true;
        saveDataToLocalStorage();
        initialContacts = cloneDeep(newContacts); // Update the initial state to the current state
      }
    }, { deep: true });

    return {
      restoreDataFromLocalStorage,
      makeCall,
      contacts,
      currentIndex
    };
  },
  mounted() {
    this.loadMessageFiles();
  },
  data() {
    return {
   
      headers: [
        { text: 'Send Msg', value: 'sendMsg', sortable: false },
        { text: 'No', value: 'no', sortable: false },
        { text: 'Project', value: 'project', sortable: false },
        { text: 'Source', value: 'source', sortable: false },
        { text: 'Name', value: 'name', sortable: false },
        { text: 'Phone', value: 'phoneNumber', sortable: false },
        { text: 'Occupation', value: 'occupation', sortable: false },
        { text: 'Email', value: 'email', sortable: false },
        { text: 'Date', value: 'date', sortable: false },
        { text: 'Message', value: 'message', sortable: false },
      ],
      availableDates: this.generateDateOptions(),
      messageEn: [],
      messageCn: [],
      messageBs: [],
      phoneNumber: '',
      sendmsg: "",
      whatsappmessage: '',
      jumpIndex: null,
      //  currentIndex: 0,
      error: null,
      // contacts: [
      //   { no: 1, project: 'abc', source: 'uom', name: 'John', phoneNumber: '+601116331800', interaction: 'hello', message: '!ei2', status: '', na1: "w", date: "10-10-2024", email: "aa@bb.com", occupation: "IT", lastCall: "10-10-2024", nextCall: "10-10-2024" },
      //   { no: 2, project: 'abc', source: 'uom', name: 'Jane', phoneNumber: '+601116331801', interaction: '', message: '!ei2', status: '', na1: "w", date: "10-10-2024", email: "aa@bb.com", occupation: "IT", lastCall: "10-10-2024", nextCall: "10-10-2024" },
      //   { no: 3, project: 'abc', source: 'uom', name: 'Alex', phoneNumber: '+601116331802', interaction: '', message: '!ei2', status: '', na1: "w", date: "10-10-2024", email: "aa@bb.com", occupation: "IT", lastCall: "10-10-2024", nextCall: "10-10-2024" }
      //   // Add more contacts as needed
      // ],
    };
  },
  computed: {
    currentContact() {
      return this.contacts[this.currentIndex] || {};
    }



  },
  methods: {
    rowClick(index) {
      // Set the selected index to highlight the row
      this.currentIndex = this.currentIndex === index ? null : index;
    },
    
   
    generateDateOptions() {
      const dates = [];
      const today = new Date();
      for (let i = 0; i < 30; i++) {
        const date = new Date(today);
        date.setDate(today.getDate() + i);
        dates.push(date.toISOString().split('T')[0]); // Format as YYYY-MM-DD
      }
      return dates;
    },
    addPlus(np) {
      if (np.substring(0, 2) === "60")
        np = "+" + np;
      return np.replaceAll("@", "");
    },
    formatString(text) {
      if (text === undefined) {
        return '';
      }

      // Remove all non-numeric characters
      text = text.replace(/\D/g, '');

      if (text.length <= 12) {
        return text.padEnd(12, '_'); // Pad with underscores if shorter
      } else {
        return text.slice(0, 12); // Truncate if longer
      }
    },
    loadMessageFiles() {
      this.loadTextFile('/messageEn.txt', 'messageEn');
      this.loadTextFile('/messageCn.txt', 'messageCn');
      //  this.loadTextFile('/messageBs.txt', 'messageBs');

    },
    loadTextFile(filePath, lngName) {
      fetch(filePath)
        .then(response => response.text())
        .then(data => {
          console.log(`File loaded successfully: ${filePath}`);
          console.log(`Data: ${data}`);
          const lines = data.split('\n');
          const dataArray = [];
          let currentObject = { code: '', msg: '' };

          if (this.projectKey === "undefined") {
            alert("ProjectKey Not defined");
            return;
          }

          lines.forEach(line => {
            if (!line.startsWith('_')) {
              if (line.startsWith('@')) {
                if (currentObject.code !== '') {
                  dataArray.push(currentObject);
                }
                const [code, msg] = line.split('|');
                currentObject = { code: code.trim().substring(1), msg: msg.trim() + "\n" };
              } else {
                currentObject.msg += line.trim().replaceAll("$plink", "https://alvintkb.github.io/?p=gyp" + this.enDate + "$phone" + this.projectKey) + "\n";
              }
            }
          });

          if (currentObject.code !== '') {
            dataArray.push(currentObject);
          }

          this[lngName].splice(0, this[lngName].length, ...dataArray);
          if (this[lngName].length == 0) {
            alert("Error! " + lngName + " no data");
          }
        })
        .catch(error => {
          console.error('Error loading text file:', error);
        });
    },
    sendmessage() {
      if (this.phoneNumber && this.whatsappmessage) {
        let msg = this.getmessage(this.whatsappmessage);

        const textarea = document.createElement('textarea');
        textarea.value = msg;
        document.body.appendChild(textarea);
        textarea.select();
        document.execCommand('copy');
        document.body.removeChild(textarea);

        const cleanedPhoneNumber = this.cleanPhoneNumber(this.phoneNumber);
        const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;
        window.open(url, '_blank');
      } else {
        alert('Please enter both telephone number and WhatsApp message.');
      }
    },
    jumpToIndex() {
      if (this.jumpIndex >= 0 && this.jumpIndex < this.contacts.length) {
        this.currentIndex = this.jumpIndex - 1;
      } else {
        console.error("Invalid index.");
      }
    },
    findContactByNumber(phoneNumber) {
      return this.contacts.find(contact => contact.phoneNumber.includes(phoneNumber));
    },
    findNumber() {
      const matchingContact = this.findContactByNumber(this.jumpIndex);
      if (matchingContact) {
        let i = this.contacts.indexOf(matchingContact);
        this.currentIndex = i;
      } else {
        console.error("No contact found with phone number containing", this.jumpIndex);
      }
    },

    sendViaWhatsApp(contact) {
      const { phoneNumber, name, language } = contact;


      if (this.validateInput(this.sendmsg)) {
        let message = "!" + language + this.sendmsg;
        let msg = this.getmessage(message);
        msg = msg.replaceAll("@name", name);
        msg = msg.replaceAll("$phone", this.formatString(contact.phoneNumber));

        const textarea = document.createElement('textarea');
        textarea.value = msg;
        document.body.appendChild(textarea);
        textarea.select();
        document.execCommand('copy');
        document.body.removeChild(textarea);

        const cleanedPhoneNumber = this.cleanPhoneNumber(phoneNumber);
        const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;
        window.open(url, '_blank');
        this.currentIndex = this.contacts.findIndex(c => c.no === contact.no);
        if (this.contacts[this.currentIndex].doc.lenght > 0)
          this.contacts[this.currentIndex].doc += "," + this.sendmsg;
        else
          this.contacts[this.currentIndex].doc += this.sendmsg;
      }
    },
    sendwhatsappmessage() {
      const { phoneNumber, na1, name, message } = this.currentContact;
      if (na1 !== 'w') {
        this.moveToNextContact();
        return;
      }
      if (this.validateInput(na1)) {
        let msg = this.getmessage(message);
        msg = msg.replaceAll("@name", name);
        msg = msg.replaceAll("$phone", this.formatString(this.contacts[this.currentIndex].phoneNumber));

        const textarea = document.createElement('textarea');
        textarea.value = msg;
        document.body.appendChild(textarea);
        textarea.select();
        document.execCommand('copy');
        document.body.removeChild(textarea);

        const cleanedPhoneNumber = this.cleanPhoneNumber(phoneNumber);
        const url = `https://api.whatsapp.com/send?phone=${cleanedPhoneNumber}&text=${encodeURIComponent(msg)}`;
        window.open(url, '_blank');

        this.moveToNextContact();
      }
    },
    getmessage(message) {
      let msg = "";

      if (message.slice(0, 2) === "!c")
        msg = this.messageCn.find(msg => msg.code === message.slice(1)).msg;
      // else if (message.slice(0, 2) === "!b")
      //   msg = this.messageBs.find(msg => msg.code === message.slice(1)).msg;
      else if (message.slice(0, 2) === "!e" || message.slice(0, 2) === "!b") {
        if (this.messageEn.length == 0) {
          alert("No Data");
          msg = "";
        }
        else
          msg = this.messageEn.find(msg => msg.code === message.slice(1)).msg;
      }
      else {
        alert("message error on 1st character, only allow e c b:" + message);
      }
      return msg;
    },
    validateInput(message) {
      if (!message) {
        alert('Please enter a message.');
        this.error = 'Please enter a message.';
        return false;
      }
      this.error = null;
      return true;
    },
    moveToNextContact() {
      let checkedContacts = 0;
      do {
        this.currentIndex++;
        if (this.currentIndex >= this.contacts.length) {
          this.currentIndex = 0;
          checkedContacts++;
          if (checkedContacts >= this.contacts.length) {
            break;
          }
        }
      } while (this.contacts[this.currentIndex].na1 !== 'w');
    },
    startProcessing() {
      this.currentIndex = 0;
      let checkedContacts = 0;
      while (this.contacts[this.currentIndex].na1 !== 'w') {
        this.moveToNextContact();
        checkedContacts++;
        if (checkedContacts >= this.contacts.length) {
          break;
        }
      }
    },
    reset() {
      this.currentIndex = 0;
    },
    loadCSVFile(event) {
      const file = event.target.files[0];
      if (!file) {
        this.error = 'Please select a file.';
        return;
      }

      Papa.parse(file, {
        header: true,
        complete: (results) => {

          this.contacts = results.data.map((contact, index) => ({
            no: index + 1,
            name: contact.name ? contact.name.trim() : '',
            language: contact.language ? contact.language.trim() : '',
            phoneNumber: contact.phoneNumber ? this.addPlus(contact.phoneNumber.trim()) : '',
            email: contact.email ? contact.email.trim() : '',
            status: contact.status ? contact.status.trim() : '',
            date: contact.date ? contact.date.trim() : "1-1-1900",
            project: contact.project ? contact.project.trim() : '',
            source: contact.source ? contact.source.trim() : '',
            doc: contact.doc ? contact.doc.trim() : '',
            called: contact.called ? contact.called.trim() : '0',
            contacted: contact.contacted ? contact.contacted.trim() : '0',
            occupation: contact.occupation ? contact.occupation.trim() : '',
            na1: contact.na1 ? contact.na1.trim() : '',
            message: contact.message ? contact.message.trim() : '',
            interaction: contact.interaction ? contact.interaction.trim() : '',
            nextCall: contact.nextCall ? contact.nextCall.trim() : "1-1-1900",
            lastCall: contact.lastCall ? contact.lastCall.trim() : "1-1-1900",
            edit: false,
            editField: ''
          }));
          // this.contacts.sort((a, b) => {
          //   // Assuming phoneNumber is numeric and non-empty strings
          //   return parseInt(a.phoneNumber) - parseInt(b.phoneNumber);
          // });
        },
        error: (error) => {
          console.error('Error parsing CSV file:', error);
          this.error = 'Error parsing CSV file.';
        }
      });

    },
    formatPhoneNumber(phoneNumber) {
      if (!phoneNumber.startsWith("@")) {
        return "@" + phoneNumber;
      }
      return phoneNumber;
    },
    exportToCSV() {

      const csvA = `source,project,name,language,phoneNumber,message,interaction,status,date,doc,called,contacted,occupation,na1,message,email`;
      const csvB = this.contacts.map(contact => {
        // Check if date is blank or undefined
        const date = contact.date ? contact.date.trim() : '';
        const currentDate = date ? date : new Date().toLocaleDateString('en-CA'); // Adjust date format as needed
        const phoneNumber = this.formatPhoneNumber(contact.phoneNumber);
        return `${contact.source},${contact.project},${contact.name},${contact.language},${phoneNumber},${contact.message},${contact.interaction},${contact.status},${currentDate},${contact.doc},${contact.called},${contact.contacted},${contact.occupation},${contact.na1},${contact.message},${contact.email}`;
      }).join('\n');

      const csvData = `${csvA}\n${csvB}`;
      //const csvOut = [...csvB];
      const blob = new Blob([csvData], { type: 'text/csv;charset=utf-8;' });
      const link = document.createElement('a');
      const url = URL.createObjectURL(blob);
      link.setAttribute('href', url);
      link.setAttribute('download', 'contacts.csv');
      link.style.visibility = 'hidden';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    editField(contact, field) {
      contact.edit = true;
      contact.editField = field;
    },
    saveField(contact) {
      contact.edit = false;
      contact.editField = '';
    }


  }


};
</script>


<style scoped>
.highlight {
  background-color: yellow;
}

.selected {
  background-color: lightblue;
}

table {
  width: 100%;
  border-collapse: collapse;
  overflow-x: auto;
}

th,
td {
  border: 1px solid #ddd;
  padding: 8px;
}

th {
  background-color: #f2f2f2;
}

.col-no {
  width: 50px;
}

.col-name {
  width: 150px;
}


.col-phone {
  width: 150px;
}

.col-email {
  width: 250px;
}

.col-status {
  width: 100px;
}

.col-date {
  width: 150px;
}

.col-na1 {
  width: 100px;
}

.bordered-input {
